<template>
	<input
		:id="field_id"
		:name="field_id"
		v-model="value"
		@focus="open_calendar"
		@keyup.enter.prevent="close_calendar"
		class="form-control"
		type="text"
	>

	<div v-show="is_opened || debug_keep_open" class="vdp-datepicker" ref="calendar_reference" >
		<div class="calendar">
			<header>
				<span class="prev" @click="prev" >
					&lt;
				</span>
				<span class="go_up" @click="go_up" >
					{{ up_the_text }}
				</span>
				<span class="next" @click="next" >
					&gt;
				</span>
			</header>

			<!-- days -->
			<div
				v-if="calendar_type === 0"
			>
				<span
					v-for="weekday in construct_week"
					class="cell day-header"
				>
					{{ weekday }}
				</span>
				<span
					v-for="day in construct_days"
					@click="day_picked(day)"
					class="cell day"
					:class="[
						day === this_day ? 'today' : '',
						day === 0 ? 'blank' : '',
						day === picked_day ? 'picked' : '',
						is_before_disabled_day(day) ? 'date_disabled' : '',
					]"
				>
					{{ day === 0 ? '' : day }}
				</span>
			</div>

			<!-- months -->
			<div
				v-if="calendar_type === 1"
			>
				<span
					v-for="(number, month) in construct_months"
					@click="month_picked(number)"
					class="cell month"
					:class="[
						number === this_month ? 'today' : '',
						is_before_disabled_month(number) ? 'date_disabled' : '',
					]"
				>
					{{ month }}
				</span>
			</div>

			<!-- years -->
			<div
				v-if="calendar_type === 2"
			>
				<span
					v-for="year in construct_years"
					@click="year_picked(year)"
					class="cell year"
					:class="[
						year === this_year ? 'today' : '',
						is_before_disabled_year(year) ? 'date_disabled' : '',
					]"
				>
					{{ year }}
				</span>
			</div>

			<footer>
				<span @click="set_today" >
					Today
				</span>
				<span @click="close_calendar" >
					Close
				</span>
			</footer>

		</div>
	</div>

</template>

<script>
	import STATE_params from '@/store/STATE_params.js';
	import Date_helpers from '@/components/forms/date_library/Date_helpers.js';
	import datepicker_range_mixin from '@/components/forms/dynamic_input_mixins/datepicker_range_mixin.js';

	export default {
		props: {
			field_id: String,
			field_data: Object,
			emit_name: String, // or undefined
		},
		components: {
		},
		emits: ['validate_global_changed', 'recalculate_product'],
		mixins: [datepicker_range_mixin],
		data() {
			return {
				is_opened: false,
				calendar_type: undefined, // 0 days, 1 months, 2 years, 3 decades
				shown: {
					day: 0,
					month: 0,
					year: 0,
					decade: 0,
				},
				picked: {
					day: 0,
					month: 0,
					year: 0,
					decade: 0,
				},
				debug_keep_open: false,
			}
		},
		created() {
			if (
				this.field_data?.type_settings?.calendar_type === 'years_first'
				||
				this.field_data?.type_settings?.calendar_type === 'no_value_show_oldies'
			)
			{
				this.calendar_type = 2;
			}
			else {
				// this.field_data?.type_settings?.calendar_type === 'standard'
				this.calendar_type = 0;
			}
			this.picked = Date_helpers.default_shown(this.value);
		},
		mounted() {
		},
		methods: {
			prev()
			{
				if (this.calendar_type === 2)
				{
					this.shown.decade -= 30;
				}
				else if (this.calendar_type === 1)
				{
					this.shown.year--;
				}
				else if (this.calendar_type === 0)
				{
					if (this.shown.month === 1)
					{
						this.shown.month = 12;
						this.shown.year--;
					}
					else
					{
						this.shown.month--;
					}
				}
			},
			next()
			{
				if (this.calendar_type === 2)
				{
					this.shown.decade += 30;
				}
				else if (this.calendar_type === 1)
				{
					this.shown.year++;
				}
				else if (this.calendar_type === 0)
				{
					if (this.shown.month === 12)
					{
						this.shown.month = 1;
						this.shown.year++;
					}
					else
					{
						this.shown.month++;
					}
				}
			},
			go_up()
			{
				if (this.calendar_type === 2) {
					return;
				}
				this.calendar_type++;
			},
			day_picked(int)
			{
				if (int === 0) {
					return;
				}
				if (this.is_before_disabled_day(int)) {
					return;
				}
				this.shown.day = int;
				this.picked = this.shown;
				this.update_input();
				this.is_opened = false;
			},
			month_picked(int)
			{
				if (this.is_before_disabled_month(int)) {
					return;
				}
				this.shown.month = int;
				this.calendar_type--;
			},
			year_picked(int)
			{
				if (this.is_before_disabled_year(int)) {
					return;
				}
				this.shown.year = int;
				this.calendar_type--;
			},
			update_input()
			{
				let day = this.picked.day.toString().padStart(2, '0');
				let month = this.picked.month.toString().padStart(2, '0');
				let year = this.picked.year.toString();
				this.value = day + '.' + month + '.' + year;
			},
			open_calendar()
			{
				this.shown = Date_helpers.default_shown(this.value);
				this.is_opened = true;
			},
			close_calendar(/*event*/)
			{
				// dont close calendar if focus was lost inside calendar
				// REMOVED SINCE CHROME CANT DETERMINE CLICKED TARGET ON BLUR, REPLACED WITH CLOSE BUTTON, REMOVE AFTER WHILE, 26.06.2023
				// @blur="close_calendar"
				// if (this.$refs.calendar_reference.contains(event.explicitOriginalTarget)) {
				// 	return;
				// }
				this.picked = Date_helpers.default_shown(this.value);
				this.update_input();
				this.calendar_type = 0;
				this.is_opened = false;
			},
			set_today()
			{
				this.shown = Date_helpers.default_shown('');
				this.calendar_type = 0;
			},
		},
		computed: {
			construct_days()
			{
				return Date_helpers.construct_days(this.shown.year, this.shown.month);
			},
			construct_week()
			{
				return Date_helpers.construct_week();
			},
			construct_months()
			{
				return Date_helpers.construct_months();
			},
			construct_years()
			{
				return Date_helpers.construct_years(this.shown.decade);
			},
			this_day()
			{
				let now = new Date();
				if (
					now.getMonth() + 1 === this.shown.month
					&&
					now.getFullYear() === this.shown.year
				) {
					return now.getDate();
				}
				return null;
			},
			picked_day()
			{
				if (this.value === '') {
					return null;
				}
				if (
					this.picked.month === this.shown.month
					&&
					this.picked.year === this.shown.year
				) {
					return this.picked.day;
				}
				return null;
			},
			this_month()
			{
				let now = new Date();
				if (
					now.getFullYear() === this.shown.year
				) {
					return now.getMonth() + 1;
				}
				return null;
			},
			this_year()
			{
				return new Date().getFullYear();
			},
			up_the_text()
			{
				if (this.calendar_type === 0)
				{
					return Date_helpers.show_month_name(this.shown.month) + ' ' + this.shown.year;
				}
				else if (this.calendar_type === 1)
				{
					return this.shown.year;
				}
				else if (this.calendar_type === 2)
				{
					return this.shown.decade + ' - ' + (this.shown.decade + 29);
				}
				else
				{
					return 'should not occur';
				}
			},
			value:
			{
				get() {
					if (this.emit_name !== undefined) {
						return STATE_params().get_user_input(this.field_id);
					}
					return STATE_params().get_form_value(this.field_id);
				},
				set(event_value) {
					STATE_params().set_form_value(this.field_id, event_value);
					if (this.emit_name !== undefined) {
						this.$emit(this.emit_name);
					}
				},
			},
		},
		watch: {
		},
	}
</script>

<style lang="scss">

	.vdp-datepicker {
		position: absolute;
		top: 54px;
		// @include media-breakpoint-down(sm) {
		// 	top: 76px;
		// }
		@media only screen and (max-width: 574px) {
			top: 76px;
		}
		* {
			box-sizing: border-box;
		}

		.calendar {
			position: absolute;
			z-index: 100;
			background: #fff;
			width: 300px;
			border: 1px solid #ccc;
			header {
				display: block;
				line-height: 40px;
				span {
					display: inline-block;
					text-align: center;
					width: 58%;
					float: left;
				}
				.prev {
					&:after {
						border-right: 10px solid #000;
						margin-left: -5px;
					}
					&.disabled {
						&:after {
							border-right: 10px solid #ddd;
						}
					}
				}
				.next {
					&:after {
						border-left: 10px solid #000;
						margin-left: 5px;
					}
					&.disabled {
						&:after {
							border-left: 10px solid #ddd;
						}
					}
				}
			}
			.disabled {
				color: #ddd;
				cursor: default;
			}
			.cell {
				display: inline-block;
				padding: 0 5px;
				width: 14.285714285714286%;
				height: 40px;
				line-height: 40px;
				text-align: center;
				vertical-align: middle;
				border: 1px solid transparent;
				&.selected {
					background: maroon;
					&:hover {
						background: maroon;
					}
					&.highlighted {
						background: maroon;
					}
				}
				&.highlighted {
					background: #cae5ed;
					&.disabled {
						color: #a3a3a3;
					}
				}
				&.grey {
					color: #888;
					&:hover {
						background: inherit;
					}
				}
				&.day-header {
					font-size: 75%;
					white-space: nowrap;
					cursor: inherit;
					&:hover {
						background: inherit;
					}
				}
				&.today {
					border-left: 1px solid var(--text);
					border-bottom: 1px solid var(--text);
				}
				&.picked {
					background-color: var(--primary_light);
				}
				&.date_disabled {
					opacity: 0.3;
				}
			}
		}
		.calendar header .prev,
		.calendar header .next {
			width: 21%;
			float: left;
			// text-indent: -10000px;
			position: relative;
		}
		.calendar header .prev:after,
		.calendar header .next:after {
			content: '';
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translateX(-50%) translateY(-50%);
			border: 6px solid transparent;
		}
		.calendar header .prev:not(.disabled),
		.calendar header .next:not(.disabled),
		.calendar header .go_up:not(.disabled) {
			cursor: pointer;
		}
		.calendar header .prev:not(.disabled):hover,
		.calendar header .next:not(.disabled):hover,
		.calendar header .go_up:not(.disabled):hover {
			background: #eee;
		}
		.calendar .cell:not(.blank):not(.disabled).day,
		.calendar .cell:not(.blank):not(.disabled).month,
		.calendar .cell:not(.blank):not(.disabled).year {
			cursor: pointer;
		}
		.calendar .cell:not(.blank):not(.disabled).day:hover,
		.calendar .cell:not(.blank):not(.disabled).month:hover,
		.calendar .cell:not(.blank):not(.disabled).year:hover {
			border: 1px solid var(--primary);
		}
		.calendar .month {
			width: 33.333%;
		}
		.calendar .year {
			width: 20%;
